body {
  font: 100% Helvetica, sans-serif;
  color: #333333; }

body {
  margin: auto;
  font-family: 'Kaushan Script', cursive;
  background-color: #7A8691; }

.wrap {
  margin: auto; }

.ms-footer {
  margin-top: 20px; }

ul {
  list-style-type: none; }

footer {
  margin-top: 40px;
  background-color: #444;
  height: 100px; }

footer .btn {
  background-color: #444;
  line-height: 86px;
  transition: 1s;
  width: 24%; }

.btn:hover {
  background-color: #2468d4;
  transition: background-color 2s; }
